export default{
	data(){
		return{
			phone:''
		}
	},
	methods:{
		sure(){
			var mPattern = /^1[34578]\d{9}$/;
			if(mPattern.test(this.phone)){
				uni.showToast({
					title:'手机格式正确',
					icon:'success'
				})
				uni.request({
					//url:'https://www.nssnail.com/wx/personal/updateUserEmail',
					method:'POST',
					data:{
						email:this.email
					},
					success: (res) => {
						console.log(res,'返回的数据')
					},
					fail: (res) => {
						console.log(res,'失败了')
					}
				})
			}else{
				uni.showToast({
					title:'手机格式错误',
					icon:'none'
				})
			}

		}
	}
}
